import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import UIkit from 'uikit';
import 'uikit/dist/css/uikit.min.css';
import Icons from 'uikit/dist/js/uikit-icons';

import Presentation from './Views/Presentation/Presentation';
import Experience from './Views/Experience/Experience';
import Contacts from './Views/Contacts/Contacts';
import PageNotFound from './Views/PageNotFound/PageNotFound';

import Navbar from './Views/Navbar/Navbar';
import Footer from './Views/Footer/Footer';

import './App.css';
import UnderDevelopment from './Views/UnderDevelopment/UnderDevelopment';

UIkit.use(Icons);

export default function App() {
  return (
    <Router>
      <Navbar />
      {false ? (
        <UnderDevelopment />
      ) : (
        <Switch>
          <Route exact path="/site/" component={Presentation} />
          <Route path="/site/contacts" component={Contacts} />
          <Route path="/site/experience" component={Experience} />
          <Route component={PageNotFound} />
        </Switch>
      )}
      <Footer />
    </Router>
  );
}
